@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&family=Roboto:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.powerX{
    font-family: 'Roboto', sans-serif;
}
.footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #000000e1;
    color: white;
    padding: 20px 0;
}

.footer > section:first-child{
    display: flex;
    justify-content: space-between;
    width: 90%;
    margin-bottom: 100px;
}

.footer > section:first-child > *{
    width: 20%;
}

.footer > section > div > p:first-child{
    font-weight: 800;
}

.footer .socialMedia{
    display: flex;
}

.footer .socialMedia > *{
    margin-right: 10px;
    font-size: 25px;
}

.footer .mobileFooter{
    display: none;
}

@media (max-width: 950px){
    .footer > section:first-child{
        flex-wrap: wrap;
        justify-content: center;
        margin-bottom: 50px;
    }

    .footer > section:first-child > *{
        width: 180px;
    }
}

@media (max-width: 650px) {
    .footer > section:first-child > div:nth-child(2),
    .footer > section:first-child > div:nth-child(3),
    .footer > section:first-child > div:nth-child(5){
        display: none;
    }

    .footer > section:first-child > *{
        width: 40%;
    }

    .footer .mobileFooter{
        display: flex;
        justify-content: center;
        width: 100%;
    }

    .footer .mobileFooter > p{
        padding: 10px;
        font-weight: 800;
        width: 100%;
    }
}
.header{
    background: url(/static/media/header_background.6d78ce2d.jpg);
    background-size: cover;
    background-position: center;
    padding: 0 60px;
    height: 700px;
}

.header .navBar{
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
}

.header .navBar > h2{
    font-size: 30px;
    color: white;
}

.header .navBar .navOption{
    display: flex;
    justify-content: space-between;
}

.header .navBar .navOption > p{
    padding: 15px 25px;
    font-weight: 600;
}

.header .navBar .navOption > p > *{
    text-decoration: none;
    color: white;
}

.header .navBar > section:last-child{
    display: none;
}

.header .headerBody{
    display: flex;
    justify-content: space-between;
    color: white;
}

.header .headerBody > *:first-child{
    width: 50%;
}

.header .headerBody h1{
    margin-top: 40px;
    font-size: 80px;
    font-family: 'Oswald', sans-serif;
    font-weight: 400;
}

.header .headerBody > section:first-child > p{
    position: relative;
    bottom: 60px;
    line-height: 2;
}

.header .headerBody > section:first-child > *:last-child > *{
    background-color: goldenrod;
    border-radius: 0;
    position: relative;
    bottom: 40px;
    padding: 5px 45px;
    font-family: 'Oswald', sans-serif;
    font-size: 25px;
    outline: none;
    border: none;
}

.header .headerBody > section:last-child > *{
    font-size: 600px;
    opacity: 10%;
    margin-top: -40px;
}

@media only screen and (max-width: 1400px) {
    .header{
        height: 650px;
    }

    .header .headerBody{
        margin-top: 10px;
    }
    
    .header .headerBody h1{
        font-size: 60px;
    }

    .header .headerBody > section:first-child > p{
        font-size: 14px;
    }

    .header .headerBody > section:last-child > *{
        font-size: 450px;
        margin-top: 0;
    }
}

@media only screen and (max-width: 1111px) {
    .header{
        height: 550px;
    }

    .header .navBar .navOption > p{
        padding: 15px 10px;
    }

    .header .headerBody{
        margin-top: 0;
    }
    
    .header .headerBody h1{
        font-size: 50px;
    }

    .header .headerBody > section:first-child > p{
        font-size: 12px;
    }

    .header .headerBody > section:first-child > *:last-child{
        padding: 5px 30px;
        font-size: 20px;
    }

    .header .headerBody > section:last-child > *{
        font-size: 350px;
    }
}

@media only screen and (max-width: 940px) {
    .header{
        height: 450px;
    }

    .header .navBar .navOption > p{
        padding: 15px 5px;
    }

    .header .headerBody{
        margin-top: -20px;
    }

    .header .headerBody h1{
        font-size: 40px;
    }

    .header .headerBody > section:first-child > p{
        font-size: 10px;
        bottom: 40px;
    }

    .header .headerBody > section:last-child > *{
        font-size: 280px;
    }
}

@media only screen and (max-width: 771px) {
