    }

    .header .headerBody > section:last-child > *{
        font-size: 250px;
        margin-right: -30px;
    }
}

@media only screen and (max-width: 690px) {
    .header{
        height: 350px;
    }

    .header .navBar{
        padding: 10px 0;
    }

    .header .navBar > h2{
        font-size: 20px;
    }

    .header .navBar > section:last-child{
        display: block;
        padding: 0;
    }

    .header .headerBody h1{
        font-size: 30px;
    }

    .header .headerBody > section:first-child > p{
        bottom: 30px;
    }

    .header .headerBody > section:first-child > *:last-child{
        font-size: 14px;
        padding: 5px 20px;
    }

    .header .headerBody > section:last-child > *{
        font-size: 200px;
    }
}

@media only screen and (max-width: 550px) {
    .header{
        height: 250px;
    }

    .header .headerBody h1{
        font-size: 20px;
    }

    .header .headerBody > section:first-child > p{
        bottom: 20px;
        line-height: 1.3;
        font-size: 8px;
    }

    .header .headerBody > section:first-child > *:last-child{
        bottom: 20px;
        padding: 2px 5px;
        font-size: 10px;
    }

    .header .headerBody > section:last-child > *{
        font-size: 150px;
    }
}

@media only screen and (max-width: 441px) {
    .header{
        padding: 0 30px;
        height: 200px;
    }

    .header .navBar{
        padding: 5px 0;
    }

    .header .navBar > h2{
        font-size: 15px;
    }

    .header .navBar > section:last-child > *{
        font-size: 12px;
    }

    .header .headerBody h1{
        font-size: 18px;
    }

    .header .headerBody > section:first-child > p{
        line-height: 1;
    }

    .header .headerBody > section:last-child > *{
        font-size: 100px;
        margin-right: -5px;
        margin-top: 20px;
    }
}
.mainBody{
    padding: 80px 100px;
}

.mainBody > section:first-child{
    display: flex;
    justify-content: space-between;
}

.mainBody > section:first-child img{
